.backdrop {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.20);
    z-index: 10;
    border-radius: 15px;
    width: 100%;
    height: 100%;
}

.modal {
    border-radius: 15px;
    width: 311px;
    height: 350px;
    position: absolute;
    background: #FFF;
    z-index: 11;
    padding: 200px 24px 32px 24px;
    box-shadow: 0px 2px 1px 0px rgba(0, 0, 0, 0.20), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);


    &__btn-close {
    position: absolute;
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    border-radius: 50px;
    color: rgba(0, 0, 0, 0.10);
    background-color: getcolor('white-color');
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

 &__svg-close {
     width: 18px;
     height: 18px;
     fill: getcolor('white-color');
     

     &:hover, &:focus {
         fill: #2196F3;
     }
 }

&__img {
    width: 311px;
    height: 169px;
    position: absolute;
    top: 0;
    left: 0;
}

 &__title {
    font-family: 'font-family', sans-serif;
    text-transform: uppercase;
    color: getcolor('black-color');
    margin-bottom: 24px;
    @include fonts(20px, 600px, 120%, -0.03px);

    &--orange {
        color: getcolor('orange-color');
    }
}

   &__input {
    width: 263px;
    height: 45px;
   }

   &__submit {
    border-radius: 100px;
    padding: 14px 28px;
    width: 100px;
    height: 45px;
    font-family: 'font-family',sans-serif;
    font-weight: 600;
    font-size: 14px;
    text-align: center;
    color: getcolor('white-color');
    background-color: getcolor('orange-color');
   }
}